import './App.css';
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Line } from 'react-chartjs-2';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
} from 'chart.js';

// Chart.js를 사용하기 위해 기본 설정 등록
ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend
);

function App() {
    const [hello, setHello] = useState('')
    const [emoteCounts, setEmoteCounts] = useState({})

    let ary = ['남자 코트 추천', '강남 우동 맛집', '파이썬독학']
    let [titles, setTitles] = useState(ary)

    useEffect(() => {
        axios.get('/api/admin/hello')
            .then(response => setHello(response.data))
            .catch(error => console.log(error))

        axios.get('/api/admin/emoteCounts')
            .then(response => setEmoteCounts(response.data))
            .catch(error => console.log(error))
    }, [])

    const sortTitles = () => {
        const sortedTitles = [...titles].sort();
        setTitles(sortedTitles);
    };

    return (
        <div className="App">
            <div className="black-nav">
                <h4>관리자 페이지 입니다.</h4>
            </div>

            <button onClick={sortTitles}>글 제목 정렬</button>

            {titles.map((title, index) => (
                <ProductList key={title} name={title} date="11월 26일" />
            ))}

            <h4>백엔드에서 가져온 데이터입니다~~ : {hello}</h4>
            <div style={{ width: '80%', margin: '0 auto' }}>
                <EmoteCountChart data={emoteCounts} />
            </div>
        </div>
    );
}

const ProductList = (
    { name },
    { date }
) => {
    const [like, setLike] = useState(0);

    return (
        <div className="list">
            <h4>
                {name}
                <span onClick={() => {
                    setLike(like + 1)
                }}> 👍 </span>
                {like}
            </h4>
            <p>
                {date} 발행
            </p>
        </div>
    );
}

function EmoteCountChart({ data }) {
    const chartData = {
        labels: Object.keys(data), // 날짜 (가로축)
        datasets: [
            {
                label: 'Emote Counts', // 그래프 제목
                data: Object.values(data), // 이모트 수치 (세로축)
                borderColor: 'rgba(75, 192, 192, 1)', // 선 색상
                backgroundColor: 'rgba(75, 192, 192, 0.2)', // 배경 색상
                tension: 0.0, // 곡선 정도
            },
        ],
    };

    const options = {
        responsive: true,
        plugins: {
            legend: { position: 'top' },
            title: { display: true, text: 'Emote Counts Over Time' },
        },
        scales: {
            x: { title: { display: true, text: 'Date' } },
            y: { title: { display: true, text: 'Counts' } },
        },
    };

    return <Line data={chartData} options={options} />;
}



export default App;